//import request from './request'
import request from '@/utils/request'
// 获取字典数据列表
export function getDictList(params) {
  return request({
    url: '/system/dict/data/list',
    method: 'get',
    params
  })
}

// 根据图片ID获取图片URL
export function getImagesByIds(ids) {
  return request({
    url: `/system/oss/listByIds/${ids}`,
    method: 'get'
  })
}

// 获取课程分类
export function getCourseCategoriesDict() {
  return request({
    url: '/system/dict/data/type/course_categories',
    method: 'get'
  })
}

// 获取题库分类
export function getRepoCategories() {
  return request({
    url: '/system/dict/data/type/question_repos_type',
    method: 'get'
  })
}

// 获取字典数据列表
export function getDictDataList(params) {
  return request({
    url: '/system/dict/data/list',
    method: 'get',
    params
  })
} 